import {MatPaginatorIntl} from '@angular/material';

export class  PaginatorEspaol extends MatPaginatorIntl { 
    itemsPerPageLabel = 'Items por Pgina'; 
    nextPageLabel  = 'Siguiente'; 
    previousPageLabel = 'Previa'; 
    firstPageLabel = "Primera Pgina";
    lastPageLabel = "ltima Pgina"

    getRangeLabel = function (page, pageSize, length) { 
        if (length === 0 || pageSize === 0) { 
             return '0 de ' + length; 
        } 
        length = Math.max(length, 0); 
        const startIndex = page * pageSize; 
        //Si el ndice de inicio excede la longitud de la lista, no intente 
        //arreglar el ndice final hasta el final
    const endIndex = startIndex < length ?
    Math.min(startIndex + pageSize, length) :  startIndex + pageSize; 
    return startIndex + 1 + ' - ' + endIndex + ' de ' + length; 
}; 
} 